import React, { Component } from 'react'
import './Home.css'

export class Home extends Component {
  render() {
    return (
      <div className="home-page">
        <div className="hero-section py-4">
          <div className="container h-100">
            <div className="row h-100">
              <div className="offset-4 col-8">
                <div className="hero-content text-light w-100">
                  <h1 className="mb-2">Coronavirus</h1>
                  <p className="mb-4 text-light">Coronaviruses are a large family of viruses which may cause illness in animals or humans.  In humans, several coronaviruses are known to cause respiratory infections ranging from the common cold to more severe diseases such as Middle East Respiratory Syndrome (MERS) and Severe Acute Respiratory Syndrome (SARS). <span className="bold">The most recently discovered coronavirus causes coronavirus disease COVID-19.</span></p>
                  <h3 className="mb-2">About this website</h3>
                  <p className="mb-2 text-light">There are so many hoax messages and rumors available on social media about Corona Virus. As a developer and a part of this society, I felt the best way possible to create an awareness and provide the correct details about this outbreak is by creating a website for it.</p>
                  <p className="bold text-light mb-4">*All the information provided in the website is taken from the official website of <a href="https://www.who.int/health-topics/coronavirus" target="_blank" rel="noopener noreferrer" className="link">World Health Organisation (WHO)</a></p>
                  
                  <a href="https://www.who.int/health-topics/coronavirus" target="_blank" rel="noopener noreferrer" className="external-link-btn external-link-btn--light">Know More</a>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        <div className="video-container">
          <div className="container p-5">
            <div className="row">
              <h4 className="mb-4">Novel coronavirus (2019-nCoV)</h4>
              <div className="embed-responsive embed-responsive-16by9">
                <iframe className="embed-responsive-item" src="https://www.youtube.com/embed/mOV1aBVYKGA?rel=0" allowfullscreen title="Novel coronavirus (2019-nCoV)"></iframe>
              </div>
            </div>
          </div>
        </div>
      </div>
      
    )
  }
}

export default Home
